@import "../../../assets/styles/variables.scss";


// .Main-logo {
  //   height: 40vmin;
  //   pointer-events: none;
  // }
  .Main{
    box-shadow: 1px 0px 6px 0px #4a4a4a;
  }
  
  
  .Main-header {
    color: $backgroundColor;
    position: relative;
    min-height: 100vh;
    width: 100%;
    display: flex;
    
    .left-side-bar {
      // box-shadow: 1px 0px 6px 0px #4a4a4a;
      min-width: 200px;
      z-index: 2;
      &-style{
        background-color: black;
      }
      // &-size{
      //   width: 100%;
      // }
      .photo{
        height: 300px;
        background-image: url("../../shared/images/profile.png");
        background-size: cover;
        position: relative;
      }
    }
    .main-body{
      &-style{
        background-color: #d6d6d6;
      }
      // &-size{
      //   width: 70%;
      // }
      .name-banner{
        position: relative;
        margin: 5% 0px 0px 5%;
        display: block;
        height: 260px;
        
        text-align: left;
        .name-content{
          font-size: 5rem;
          display: flex;
          line-height: 4rem;
          margin-bottom: 10px;
          font-weight: 600;
        }
        .profession{
          background-color: black;
          color: $font-color;
          font-size: 2rem;
          padding: 5px 20px;
        }
        .personal-info{
          text-align: center;
          padding: 5px 0px;
          font-size: 1.2rem;
        }
      }
      
    }
  }
  
  .banner{
    height: 125px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $background-color-transparent;
    color: white;
    text-shadow: 2px 0px $font-shadow-color;
    &-pprofile{
      width: 100%;
      bottom: 0px;
      position: absolute;
      .content{
        text-align: end ;
        margin-left: 40%;
        font-size: 2rem;
        width: 50%;
      }
    }
    &-pdescription{
      font-size: 1.3rem;
      text-align: left;
      padding: 0px 30px;
    }
  }
  
  .Main-link {
    color: #61dafb;
  }

  .language{
    position: absolute;
    background-color: red;
    z-index: 1;
    right: 0px;
  }

  
@media only screen and (min-width: 992px){
  .Main-header {
    .left-side-bar {
      
      &-size{
        width: 30%;
      }
      .photo{
        height: 450px;
      }
    }
    .main-body{
      
      &-size{
        width: 70%;
      }
      .name-banner{
        position: relative;
        margin: 90px 0px 0px 90px;
        display: block;
        height: 235px;
      }
    }
  }
}
